import stringSearch from "@/assets/stringSearch";
import { IfilesBases, IfilesBasesConfig, IfilesRelatedPerformerBases } from "@/dataInterface/bases.interface";
import { Iperformer } from "@/dataInterface/performer.interface";
import { Itag, ItagClass } from "@/dataInterface/tag.interface";
import { basesServerData } from "@/server/bases.server";
import { performerServerData } from "@/server/performer.server";
import { defineStore } from "pinia";
export const basesStore = defineStore('filesBases', {
    state: () => ({
        currentFilesBases: {} as IfilesBases,
        filesBasesList: [] as Array<IfilesBases>,
        filesRelatedPerformerBases: [] as Array<IfilesRelatedPerformerBases>,
        performerList: [] as Array<Iperformer>,
        tagClassList: [] as Array<ItagClass>,
        tagList: [] as Array<Itag>,
        filesBasesSetting: {} as IfilesBasesConfig,
    }),
    getters: {
        getCurrentFilesBasesMainPerformerBasesId: function (state) {
            for (const info of state.filesRelatedPerformerBases) {
                if (info.filesBases_id == state.currentFilesBases.id && info.main) {
                    return info.performerBases_id;
                }
            }
            return undefined;
        },
        getPerformerListPaging: function (state) {
            return (page = 1, limit = 100, searchText = '') => {
                const mainPerformerBases_id = this.getCurrentFilesBasesMainPerformerBasesId;
                const dataList: Array<Iperformer> = [];
                if (mainPerformerBases_id == undefined) {
                    return { dataList, count: 0 };
                }
                let count = 0;
                const start = (page - 1) * limit;
                const end = start + limit;
                for (const performer of state.performerList) {
                    if (performer.performerBases_id == mainPerformerBases_id) {
                        let addStatus = true;
                        if (searchText != '') {
                            if (!stringSearch(searchText, performer.name + performer.aliasName)) {
                                addStatus = false;
                            }
                        }
                        if (addStatus) {
                            if (count >= start && count < end) {
                                dataList.push(performer);
                            }
                            count++;
                        }
                    }
                }
                return { dataList, count };
            }
        },
        getPerformerInfoById: function (state) {
            return (id: string): Iperformer | undefined => {
                for (let i = 0; i < state.performerList.length; i++) {
                    if (state.performerList[i].id == id) {
                        return state.performerList[i];
                    }
                }
                return undefined;
            };
        },
        getPerformerListByFilesBasesId: function (state) {
            const performerBases_arr: Array<string> = [];
            state.filesRelatedPerformerBases.forEach((relatedInfo: IfilesRelatedPerformerBases) => {
                if (relatedInfo.filesBases_id == state.currentFilesBases.id) {
                    performerBases_arr.push(relatedInfo.performerBases_id);
                }
            })
            return state.performerList.filter(item => item.status && performerBases_arr.includes(item.performerBases_id))
        },
        getTagClassListByCurrentFilesBases: function (state) {
            return state.tagClassList.filter(item => item.filesBases_id == state.currentFilesBases.id && item.status);
        },
        getTagListByTagClassId: function (state) {
            return (tagClass_id: string): Array<Itag> => {
                return state.tagList.filter(item => item.tagClass_id == tagClass_id && item.status);
            }
        },
        getTagInfoById: function (state) {
            return (id: string): Itag | undefined => {
                for (let i = 0; i < state.tagList.length; i++) {
                    if (state.tagList[i].id == id) {
                        return state.tagList[i];
                    }
                }
                return undefined;
            };
        },
    },
    actions: {
        init: async function () {
            try {
                const promises = [
                    this.setFilesBasesList(),
                    this.setFilesRelatedPerformerBases(),
                    this.setPerformerList(),
                    this.setTagClassList(),
                    this.setTagList()
                ]
                await Promise.all(promises);
            } catch (error) {
                console.error(error);
            }
        },
        switchDatabases: async function (id: string) {
            try {
                for (const filesBases of this.filesBasesList) {
                    if (filesBases.id == id) {
                        this.currentFilesBases = filesBases;
                        await this.setFilesBasesSetting(id);
                    }
                }
            } catch (error) {
                console.error(error);
            }
        },
        setFilesBasesList: async function () {
            const dataList = await basesServerData.getFilesBases();
            if (dataList.length > 0) {
                this.filesBasesList = dataList;
                this.currentFilesBases = dataList[0];
                await this.setFilesBasesSetting(this.currentFilesBases.id);
            }
        },
        setFilesRelatedPerformerBases: async function () {
            const dataList = await basesServerData.getFilesRelatedPerformerBases();
            if (dataList.length > 0) {
                this.filesRelatedPerformerBases = dataList;
            }
        },
        setPerformerList: async function () {
            const dataList = await performerServerData.getPerformerList();
            if (dataList.length > 0) {
                this.performerList = dataList;
            }
        },
        setTagClassList: async function () {
            const dataList = await basesServerData.getTagClass();
            if (dataList.length > 0) {
                this.tagClassList = dataList;
            }
        },
        setTagList: async function () {
            const dataList = await basesServerData.getTag();
            if (dataList.length > 0) {
                this.tagList = dataList;
            }
        },
        setFilesBasesSetting: async function (currentFilesBases_id: string) {
            this.filesBasesSetting = await basesServerData.getFilesBasesSetting(currentFilesBases_id);
        },
    }
});