import { _ajax } from "@/assets/ajax"
import { Iperformer } from "@/dataInterface/performer.interface";
const performerServerData = {
    getPerformerList: async () => {
        const dataList = await _ajax<Array<Iperformer>>({
            method: 'get',
            url: '/api/performer',
        });
        return dataList;
    }
}

export { performerServerData }