import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import IndexView from '../views/IndexView.vue'
import LoginView from '../views/LoginView.vue'
import playAtlasView from '@/views/playAtlasView.vue'
import playComicView from '@/views/playComicView.vue'
import playVideoView from '@/views/playVideoView.vue'
import playVideoLinkView from '@/views/playVideoLinkView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: IndexView,
  },
  {
    path: '/Login',
    name: 'LoginView',
    component: LoginView,
  },
  {
    path: '/playAtlasView/:id',
    name: 'playAtlas',
    component: playAtlasView
  },
  {
    path: '/playComicView/:id',
    name: 'playComic',
    component: playComicView
  },
  {
    path: '/playVideoLinkView/:id',
    name: 'playVideoLink',
    component: playVideoLinkView
  },
  {
    path: '/playVideoView/:mode/:id',
    name: 'playVideo',
    component: playVideoView
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
