import { _ajax } from "@/assets/ajax"
const loginServerData = {
    needLogin: async function () {
        return await _ajax<boolean>({
            method: 'get',
            url: '/api/needLogin',
        });
    },
    login: async function (password: string) {
        return await _ajax<boolean>({
            method: 'post',
            url: '/api/login',
            data: {
                password: password
            }
        });
    },
}

export { loginServerData }