import { _ajax } from "@/assets/ajax"
import { IfilesBases, IfilesBasesConfig, IfilesRelatedPerformerBases } from "@/dataInterface/bases.interface";
import { Itag, ItagClass } from "@/dataInterface/tag.interface";

const basesServerData = {
    getFilesBases: async function () {
        return await _ajax<Array<IfilesBases>>({
            method: 'get',
            url: '/api/filesBases',
        });
    },
    getFilesRelatedPerformerBases: async function () {
        return await _ajax<Array<IfilesRelatedPerformerBases>>({
            method: 'get',
            url: '/api/filesRelatedPerformerBases',
        });
    },
    getFilesBasesSetting: async function (filesBases_id: string) {
        return await _ajax<IfilesBasesConfig>({
            method: 'get',
            url: `/api/filesBasesSetting/${filesBases_id}`,
        });
    },
    getTagClass: async function () {
        return await _ajax<Array<ItagClass>>({
            method: 'get',
            url: `/api/tagClass`,
        });
    },
    getTag: async function () {
        return await _ajax<Array<Itag>>({
            method: 'get',
            url: `/api/tag`,
        });
    },
}

export { basesServerData }