enum EDeviveType {
    mobile,
    tablet,
    pc,
}
const getDeviveType = () => {
    const width = getDeviveWidth();
    if (width < 768) {
        return EDeviveType.mobile;
    } else if (width >= 768 && width < 1024) {
        return EDeviveType.tablet;
    }
    return EDeviveType.pc;
}
const getDeviveWidth = () => {
    return window.innerWidth;
}

export { EDeviveType, getDeviveType, getDeviveWidth }