import { _ajax } from "@/assets/ajax"
import { IsearchCondition } from "@/dataInterface/common.interface";
import { AjaxResDramaSeriesImageData, AjaxResDramaSeriesVideoData, AjaxResResroucesData, Iresources } from "@/dataInterface/resources.interface"
const resourcesServerData = {
    resourcesList: async (filesBasesId: string, page: number, limit: number, condition: IsearchCondition, sortMode = 'desc') => {
        return await _ajax<AjaxResResroucesData>({
            method: 'post',
            url: `/api/resroucesDataList/${filesBasesId}/${page}/${limit}/${sortMode}`,
            data: {
                condition: JSON.stringify(condition)
            }
        });
    },
    resourceInfo: async (id: string) => {
        return await _ajax<Iresources>({
            method: 'get',
            url: `/api/resroucesInfo/${id}`,
        })
    },
    resourceDramaSeriesImageInfo: async (dramaSeriesId: string) => {
        return await _ajax<AjaxResDramaSeriesImageData>({
            method: 'get',
            url: `/api/resourceDramaSeriesImageInfo/${dramaSeriesId}`,
        })
    },
    resourceDramaSeriesVideoInfo: async (dramaSeriesId: string, mode: string) => {
        return await _ajax<AjaxResDramaSeriesVideoData>({
            method: 'get',
            url: `/api/resourceDramaSeriesVideoInfo/${mode}/${dramaSeriesId}`,
        })
    }
}

export { resourcesServerData }